.modal-wrapper {
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, .2);
}

.modal {
    position: relative;
    display: inline-block;
    padding: 30px;
    background-color: #fff;
    border-radius: 5px;
}


